<template>
  <div class="team-list">
    <!--    -->
    <div class="tag-section">
      <button @click="openTagModal" class="tag-input-button"> </button>
      <button @click="openCreateTeamModal" class="tag-input-button"> </button>
    </div>

    <div class="team" v-for="team in filterTeams()" :key="team.name">
      <h3>{{ team.teamName }}</h3>
      <div class="date-section">
        <font-awesome-icon :icon="['fas', 'calendar-days']"/>
        {{ team.startDate }} ~ {{ team.endDate }}
      </div>
      <p>{{ team.description }}</p>
      <button @click="openJoinModal(team)">Join</button>
    </div>

    <!--    -->
    <div class="modal" v-if="showTagModal">
      <div class="modal-content">
        <span class="cancel-icon" @click="closeTagModal">&times;</span>
        <h3>  ?</h3>
        <input
            type="text"
            v-model="teamTag"
            placeholder="  ."
            class="tag-input"
        />
        <button @click="submitTag"></button>
      </div>
    </div>

    <!--    -->
    <div class="modal" v-if="showCreateTeamModal">
      <div class="modal-content">
        <span class="cancel-icon" @click="closeCreateTeamModal">&times;</span>
        <h3> </h3>
        <input
            type="text"
            v-model="createTeamName"
            placeholder=" ."
            class="tag-input"
        />
        <input
            type="text"
            v-model="createTeamDescription"
            placeholder="    ."
            class="tag-input"
        />
        <div class="mt-3">
          <Datepicker v-model="startDate"/>
          <Datepicker v-model="endDate"/>
        </div>

        <span>  ?</span>
        <input type="checkbox" @change="changeOpenOption(createTeamOpen)"/> <br/>
        <button @click="submitCreate">Create</button>
      </div>
    </div>

    <!--    -->
    <div class="modal" v-if="showJoinModal">
      <div class="modal-content">
        <span class="cancel-icon" @click="closeJoinModal">&times;</span>
        <h3> ?</h3>
        <input
            type="text"
            v-model="introduce"
            placeholder="  !!"
            class="tag-input"
        />
        <button @click="submitJoin">Submit</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Datepicker from "vue3-datepicker";
import {apiURL} from "@/services/apiService";

export default {
  name: 'TeamListView',

  data() {
    return {
      apiUrl: apiURL,
      teams: [], //     
      searchQuery: '', //   
      showTagModal: false, //      
      showJoinModal: false, //      
      showCreateTeamModal: false, //      
      selectedTeam: null, //    
      teamTag: '', //    
      createTeamName: '',
      createTeamDescription: '',
      createTeamOpen: false,

      //    
      startDate: new Date(),
      endDate: new Date(),

      //    
      introduce: '',
      submitTeamId: '',
    };
  },
  components: {
    Datepicker,
  },
  // Vue API       .
  //    .
  async mounted() {
    try {
      const response = await axios.get(this.apiUrl + '/team/list', {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        }
      })
      this.teams = response.data;
      console.log('Team list:', this.teams);
    } catch (error) {
      console.error('Error fetching team list:', error);
    }
  },
  methods: {
    //     .
    filterTeams() {
      if (!this.searchQuery) return this.teams;
      return this.teams.filter((team) =>
          team.tags.some((tag) => tag.toLowerCase().includes(this.searchQuery.toLowerCase()))
      );
    },
    openJoinModal(team) {
      this.selectedTeam = team;
      this.showJoinModal = true;
      this.submitTeamId = team.id;
    },
    closeJoinModal() {
      this.selectedTeam = null;
      this.showJoinModal = false;
      this.submitTeamId = '';
    },
    submitJoin() {
      axios.post(this.apiUrl + '/team/join/' + this.submitTeamId, this.introduce, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
        },
      }).then((response) => {
        console.log(response);
      }).catch((error) => {
        console.error(error);
      });

      this.closeJoinModal();
    },

    openTagModal() {
      this.showTagModal = true;
    },
    closeTagModal() {
      this.showTagModal = false;
    },
    async submitTag() {
      if (!confirm('?')) {
        return;
      }

      console.log('Submitted team tag:', this.teamTag);
      try {
        await axios.post(this.apiUrl + '/team/private-join/' + this.teamTag, null, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
          },
        });
        alert(' .');
      } catch (e) {
        console.log(e);
      }
      this.closeTagModal();
    },

    openCreateTeamModal() {
      console.log(this.endDate);

      this.showCreateTeamModal = true;
    },
    closeCreateTeamModal() {
      console.log(this.endDate);
      this.showCreateTeamModal = false;
    },
    changeOpenOption(createTeamOpen) {
      this.createTeamOpen = !createTeamOpen;
    },

    async submitCreate() {
      const teamDto = {
        teamName: this.createTeamName,
        description: this.createTeamDescription,
        isOpen: this.createTeamOpen,
        startDate: this.startDate,
        endDate: this.endDate,
        goalCount: this.goalCount,
      };

      axios.post(this.apiUrl + `/team/create`,
          teamDto, {
            headers: {
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
            },
          }).then(() => {
        alert('  ');
      }).catch((error) => {
        console.error(error);
      });
      console.log('Submitted team name:', this.teamTag);
      this.closeCreateTeamModal();
    },

    formatDate(date) {
      var yyyy = date.getFullYear().toString();
      var mm = (date.getMonth() + 1).toString(); // getMonth() is zero-based
      var dd = date.getDate().toString();

      // Pad mm and dd with leading zeros if necessary
      mm = mm[1] ? mm : '0' + mm[0];
      dd = dd[1] ? dd : '0' + dd[0];

      return yyyy + '-' + mm + '-' + dd; // Concatenate yyyy, mm, and dd to get yyyymmdd format
    },
  },
};
</script>

<style scoped>
.team-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 16px;
  margin-bottom: 16px;
  width: 80%;
  border: 2px solid #20c997;
  border-radius: 8px;
}

.team button {
  width: 80%;
  display: inline-block;
  padding: 10px 16px;
  border: 2px solid #42b983;
  border-radius: 4px;
  color: #42b983;
  background-color: transparent;
  transition: background-color 0.3s;
}

.team button:hover {
  background-color: #20c997;
  color: #fff;
}

.date-section {
  font-size: 12px;
  color: #6699CC;
}

.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #fefefe;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.tag-section {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.tag-input-button {
  background-color: #00b9b9;
  border: none;
  color: white;
  padding: 12px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 8px 5px;
  cursor: pointer;
  border-radius: 5px;
}

.tag-input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

</style>
