<template>
  <div class="calendar box">
    <div class="calendar-title">
      {{ formatDate(selectedDate) }} TodoList
    </div>
    <ul class="todo-list">
      <!--        -->
      <li v-for="(todo, index) in todos" :key="index">
        <span :class="{ 'completed': todo.dailyCheck?.completed }">{{ todo.title }}</span>
        <input type="checkbox" :checked="todo.dailyCheck?.completed" @change="toggleCompleted(todo)"/>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
import {apiURL} from "@/services/apiService";

export default {
  data() {
    return {
      apiUrl: apiURL,
      isRequestPending: false,  //     
      selectedDate: new Date(),
      todos: [],
    };
  },
  methods: {
    formatDate(date) {
      const month = date.getMonth() + 1;
      const day = date.getDate();
      return `${month} ${day}`;
    },
    async toggleCompleted(todo) {
      if (this.isRequestPending) {
        //         
        return;
      }
      this.isRequestPending = true;  //   
      if (todo.dailyCheck === null) {
        // dailyCheck    
        todo.dailyCheck = {
          completed: true,
          //     
        };
        // POST    
        await axios.post(`${this.apiUrl}/goal/${todo.id}/daily`, null, {
          //     
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
          }
        });

      } else {
        // dailyCheck   isCompleted  
        todo.dailyCheck.completed = !todo.dailyCheck.completed;
        // PUT    
        await axios.put(`${this.apiUrl}/goal/${todo.id}/daily`, todo, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
          }
        });
      }
      //     `isRequestPending` `false` 
      console.log(this.isRequestPending);
      this.isRequestPending = false;
      console.log(this.isRequestPending);
    }
  },
  mounted() {
    axios.get(this.apiUrl + '/goal/todo-list', {
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + localStorage.getItem('user-token'),
      },
    }).then(response => {
      console.log(response.data);
      this.todos = response.data;
      console.log(response.data[0].dailyCheck.completed);
    }).catch(error => {
      console.log(error);
    });

  },
};
</script>

<style scoped>
.calendar {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.calendar-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.todo-list {
  list-style-type: none;
  padding: 0;
  width: 100%;
}

.todo-list li {
  font-size: 18px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.todo-list input[type="checkbox"] {
  margin-right: 10px;
}


.todo-list .completed {
  text-decoration: line-through;
  color: #ccc;
}

.todo-list .completed-before {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #ccc;
  top: 50%;
}

.box {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin: 15px;
}
</style>
